.button {
   transition: background-color 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
   border-radius: 5px;
   padding: 4px 5px;
   background-color: transparent;
   border: 2px solid;
   font-weight: 500;
   font-size: 12px;
   line-height: 1.33;
   align-items: center;
   letter-spacing: 0.06em;
   text-transform: uppercase;
   cursor: pointer;
 
   &:hover,
   &:focus {
    
    background-color: $hover-active-btn;
   }
 }

.header__btn {
  width: 129px;
  height: 44px;

  border-color: $header-btn-border;
  color: $header-btn-text;

   @include media('min', 'tablet') {
      width: 152px;
    } 
   @include media('min', 'desktop') {
      width: 149px;
    }

   &--active {
   
      background-color: $accent-bg-color;
      border: none;
      &:hover,
      &:focus {
         background-color: $hover-active-btn;
      } 
   }
   &--margin{
    margin-right: 20px;
    @include media('min', 'tablet') {
      margin-right: 32px;
    }
  
    @include media('min', 'desktop') {
    margin-right: 16px;
    }
  }
}

.modal__btn {
  width: 110px;
  height: 44px;
  border-color: $modal-btn-border;
    color: $modal-regular-btn-text;
  @include media('min', 'tablet') {
    width: 125px;
  } 
  @include media('min', 'desktop') {
    width: 136px;
  }
  &--active{
    // border:none;
    // background-color: $accent-bg-color;
    // color: $modal-active-btn-text;
    
    &:hover,
    &:focus {
      border:none;
      color: $modal-active-btn-text;
      background-color: $hover-active-btn;
    }
  }
  
  &--margin{
    margin-right: 20px;
        @include media('min', 'tablet') {
          margin-right: 15px;
    }

    // &:hover,
    // &:focus {
    //   border:none;
    //   color: $modal-active-btn-text;
    //   background-color: $hover-active-btn;
    // }
  }

  &--added-btn {
    background-color: green;
    cursor: default;

    
   &:hover,
   &:focus {
    background-color: green;
   }
  }

  &--block-btn {
    background-color: transparent;
    cursor: default;

    
   &:hover,
   &:focus {
    background-color: transparent;
   }
  }

  
}

.up-button {
  position: fixed;
  right:  13px;
  bottom: 63px;
  z-index: 2;

  border-radius: 50%;
  border: 3px solid $header-btn-border;
  display: inline-block;
  width:  42px;
  height: 42px;
  color: $modal-regular-btn-text;
  margin: 0;
  padding: 0;
  
  background-color: transparent;
  animation: animate-up 2000ms infinite 200ms;
  box-shadow: inset 0 0 10px 5px $accent-bg-color; 
  cursor: pointer;

  @include media('min', 'tablet') {
    width:  36px;
    height: 36px;
  }
  @include media('min', 'desktop') {
    color: white;
  }

  &:hover,
  &:focus {
    border-color: $hover-active-btn;
    transform: scale(1.1);
    color: $accent-bg-color; 
    font-weight: 700;
    box-shadow: inset 0 0 10px 5px $modal-regular-btn-text;      
  }
  &--is-hidden {
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    display: none;
  }
  
}

body.dark .up-button {
  
  border-color: $header-btn-border;
  color: $header-btn-text;
  &:hover,
  &:focus {
    border-color: $accent-bg-color;
    transform: scale(1.1);
    color: $accent-bg-color; 
    font-weight: 700; 
    box-shadow: inset 0 0 10px 5px $header-btn-border;     
  }
  
}

body.fixed .up-button {
  opacity: 0;
  cursor: none;
}

@keyframes animate-up {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

