.up-button {
  z-index: 2;
  width: 42px;
  height: 42px;
  color: #000;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 3px solid #fff;
  border-radius: 50%;
  margin: 0;
  padding: 0;
  animation: animate-up 2s .2s infinite;
  display: inline-block;
  position: fixed;
  bottom: 63px;
  right: 13px;
  box-shadow: inset 0 0 10px 5px #ff6b01;
}

@media screen and (min-width: 768px) {
  .up-button {
    width: 36px;
    height: 36px;
  }
}

@media screen and (min-width: 1024px) {
  .up-button {
    color: #fff;
  }
}

.up-button:hover, .up-button:focus {
  color: #ff6b01;
  border-color: #ec6200;
  font-weight: 700;
  transform: scale(1.1);
  box-shadow: inset 0 0 10px 5px #000;
}

.up-button--is-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  display: none;
}

body.dark .up-button {
  color: #fff;
  border-color: #fff;
}

body.dark .up-button:hover, body.dark .up-button:focus {
  color: #ff6b01;
  border-color: #ff6b01;
  font-weight: 700;
  transform: scale(1.1);
  box-shadow: inset 0 0 10px 5px #fff;
}

body.fixed .up-button {
  opacity: 0;
  cursor: none;
}

@keyframes animate-up {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(-45deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

