.load{
   display: none;
}

.loader-block{
   display: flex;
   justify-content: center;
   padding-top: 20px;
}

.loader-block-modal{
  display: flex;
  justify-content: center;
  position: fixed;
    width: 100%;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// --------------  1  ----------------

.loader {
   width: 30px;
   height: 30px;
   border-radius: 50%;
   display: inline-block;
   border-top: 3px solid $primary-bg-color;
   border-right: 3px solid transparent;
   box-sizing: border-box;
   animation: rotation 0.6s linear infinite;
 }
 .loader::after {
   content: '';  
   box-sizing: border-box;
   position: absolute;
   left: 0;
   top: 0;
   width: 30px;
   height: 30px;
   border-radius: 50%;
   border-bottom: 3px solid $accent-bg-color;
   border-left: 3px solid transparent;
 }

